import React from "react"
import NewsDetailClient from "@/components/NewsDetail"
import { newsData } from "@/data/news"

export function generateStaticParams() {
  return newsData.map((n) => ({ slug: n.slug }))
}

export default function Page({ params }: { params?: { slug?: string } }) {
  const slug = params?.slug

  if (!slug) {
    return (
      <div className="p-12 text-center">
        <h2 className="text-xl font-semibold">News not found</h2>
        <p className="text-sm text-stone-500">No news identifier was provided.</p>
      </div>
    )
  }

  const exists = newsData.some((n) => n.slug === slug)
  if (!exists) {
    return (
      <div className="p-12 text-center">
        <h2 className="text-xl font-semibold">News not found</h2>
        <p className="text-sm text-stone-500">The article "{slug}" does not exist.</p>
      </div>
    )
  }

  return <NewsDetailClient slug={slug} />
}